import { createRouter, createWebHashHistory } from "vue-router";
import { useUserStore } from "../store/useUserStore";

const routes = [
  {
    path: "/",
    name: "Home",
    component: () =>
      import(/* webpackChunkName: "home" */ "../views/home/Home"),
  },
  {
    path: "/cartList",
    name: "CartList",
    component: () =>
      import(/* webpackChunkName: "cartList" */ "../views/cartList/CartList"),
  },
  {
    path: "/orderConfirmation/:id/:addressId?",
    name: "OrderConfirmation",
    component: () =>
      import(
        /* webpackChunkName: "orderConfirmation" */ "../views/orderConfirmation/OrderConfirmation"
      ),
  },
  {
    path: "/orderList",
    name: "OrderList",
    component: () =>
      import(
        /* webpackChunkName: "orderList" */ "../views/orderList/OrderList"
      ),
  },
  {
    path: "/orderDetail/:id",
    name: "OrderDetail",
    component: () =>
      import(
        /* webpackChunkName: "orderDetail" */ "../views/orderDetail/OrderDetail"
      ),
  },
  {
    path: "/shop/:id",
    name: "Shop",
    component: () =>
      import(/* webpackChunkName: "shop" */ "../views/shop/Shop"),
  },
  {
    path: "/register",
    name: "Register",
    component: () =>
      import(/* webpackChunkName: "register" */ "../views/register/Register"),
    beforeEnter(to, from, next) {
      const { isLogin } = localStorage;
      isLogin ? next({ name: "Home" }) : next();
    },
  },
  {
    path: "/login",
    name: "Login",
    component: () =>
      import(/* webpackChunkName: "login" */ "../views/login/Login"),
    beforeEnter(to, from, next) {
      const { isLogin } = localStorage;
      isLogin ? next({ name: "Home" }) : next();
    },
  },
  {
    path: "/chooseAddressList/:shopId",
    name: "ChooseAddressList",
    component: () =>
      import(
        /* webpackChunkName: "chooseAddressList" */ "../views/chooseAddressList/ChooseAddressList"
      ),
  },
  {
    path: "/myAddressList",
    name: "MyAddressList",
    component: () =>
      import(
        /* webpackChunkName: "myAddressList" */ "../views/myAddressList/MyAddressList"
      ),
  },
  {
    path: "/upsertAddress/:id?",
    name: "UpsertAddress",
    component: () =>
      import(
        /* webpackChunkName: "upsertAddress" */ "../views/upsertAddress/UpsertAddress"
      ),
  },
  {
    path: "/person",
    name: "PersonalInfo",
    component: () =>
      import(
        /* webpackChunkName: "person" */ "../views/personalInfo/PersonalInfo"
      ),
  },
  {
    path: "/shop/register",
    name: "ShopRegister",
    component: () =>
      import(
        /* webpackChunkName: "shopRegister" */ "../views/shop/ShopRegister"
      ),
  },
  {
    path: "/shop/edit",
    name: "ShopEdit",
    component: () =>
      import(/* webpackChunkName: "shopEdit" */ "../views/shop/ShopEdit"),
    meta: {
      requireAuth: true,
    },
  },
  {
    path: "/shop/audit",
    name: "ShopAudit",
    component: () =>
      import(/* webpackChunkName: "shopAudit" */ "../views/shop/ShopAudit"),
    meta: {
      requireRoles: ["admin", "sales"],
    },
  },
  {
    path: "/shop/management",
    name: "ShopManagement",
    component: () =>
      import(
        /* webpackChunkName: "shopManagement" */ "../views/shop/ShopManagement"
      ),
    meta: {
      requireAuth: true,
    },
  },
  {
    path: "/shop/product/add/:id",
    name: "ProductAdd",
    component: () =>
      import(/* webpackChunkName: "productAdd" */ "../views/shop/ProductAdd"),
    meta: {
      requireAuth: true,
    },
  },
  {
    path: "/shop/tag/manager/:id",
    name: "TagManager",
    component: () =>
      import(
        /* webpackChunkName: "tagManager" */ "../views/shop/TagManager.vue"
      ),
  },
  {
    path: "/shop/product/edit/:id",
    name: "ProductEdit",
    component: () =>
      import(
        /* webpackChunkName: "productEdit" */ "../views/shop/ProductEdit.vue"
      ),
    meta: {
      requireAuth: true,
    },
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  const userStore = useUserStore();
  // 判断登录或注册页面
  const { name } = to;
  const isLoginOrRegister = name === "Login" || name === "Register";
  // 放行已登录或register页面
  !userStore.checkTokenExpiration() || isLoginOrRegister
    ? next()
    : next({ name: "Login" });
});

export default router;
