import { defineStore } from "pinia";

export const useUserStore = defineStore("user", {
  state: () => ({
    addressList: [],
    token: localStorage.getItem("token") || null,
    user: JSON.parse(localStorage.getItem("user")) || {},
    tokenExpiration: localStorage.getItem("tokenExpiration") || null, // 添加token过期时间
  }),
  actions: {
    login(token, user) {
      this.token = token;
      this.user = user;
      // 设置token过期时间，7天后过期
      const expirationTime = Date.now() + 7 * 24 * 60 * 60 * 1000; // 7天毫秒数
      this.tokenExpiration = expirationTime;

      localStorage.setItem("user", JSON.stringify(user));
      localStorage.setItem("token", token);
      localStorage.setItem("tokenExpiration", expirationTime);
    },
    logout() {
      this.token = null;
      this.user = {};
      this.tokenExpiration = null;
      localStorage.removeItem("token");
      localStorage.removeItem("user");
      localStorage.removeItem("tokenExpiration");
    },
    changeAddressList(addressList) {
      this.addressList.splice(0, this.addressList.length, ...addressList);
    },
    checkTokenExpiration() {
      if (this.token && this.tokenExpiration) {
        const currentTime = Date.now();
        if (currentTime > this.tokenExpiration) {
          this.logout(); // Token已过期，执行登出操作
          return true; // 表示token已过期
        }
      }
      return false; // 表示token未过期或不存在
    },
  },
});
